.sign-up {
    display: flex;
    height: 90vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
  }
  
  .sign-up {
    background-image: url('/images/login.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }

  @media screen and (max-width: 960px) {
    .sign-up {
      font-size: 60px;
    }
  }